@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-size: 14px;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  background: #19191b;
}


.main{margin:4rem 0}.main__select-user{display:flex;justify-content:flex-end;margin:2rem 0}
.container{width:1200px;padding:0 15px;margin:0 auto}
.sum__wrapper{width:300px;margin-top:100px}.sum__item{display:flex;justify-content:space-between;align-items:center}.sum__item:not(:first-child){margin-top:20px}.sum__item-title{color:#fff;opacity:.5}.sum__item-value{color:#fff}
.select{background:#303030;padding:10px 20px;color:#fff;border:none;outline:none;border-radius:20px;position:relative;cursor:pointer}.select__wrapper{display:flex;justify-content:space-around;align-items:center}.select__wrapper>img{height:8px;margin-left:10px}.select__options{position:absolute;width:100%;background:#fff;color:#000;text-align:left;top:130%;left:0;transition:1s ease;visibility:hidden;opacity:0}.select__options--active{visibility:visible;opacity:1}.select__option{padding:15px 10px}.select__option:hover{color:#fff;background:#000}
.table{width:100%;text-align:center;background:#303030;padding:0 2rem;border-radius:16px;color:#979797;border-collapse:separate}.table__coin{text-align:left}.table__coin-cell{display:flex;align-items:center;color:#faa52b}.table__icon{width:20px;margin-right:10px}td{padding:1.5rem 0;vertical-align:middle;border-bottom:1px solid #454545}td:first-child{text-align:left}tr>td:nth-child(even){background:#3a3a3a}.green{animation:green-anim 1.5s}.red{animation:red-anim 1.5s}@keyframes green-anim{from{color:#13c41b}to{color:#979797}}@keyframes red-anim{from{color:#da0d0d}to{color:#979797}}
